<template>
    <div class="modal-layout">
    <!-- <div class="darken" @click="closeModal"> -->
    <div class="darken">
        <div class="new-modal bonus-withdrawal-modal" @click.stop="">
            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            </div>
            <div class="modal-content">
        <p class="window-title">{{$localizeFilter('withdrawal_confirmation')}}</p>
        <p class="window-text" style="margin-bottom:35px;">{{$localizeFilter('withdrawal_confirmation_text')}} <span class="bold">{{email}}</span></p>

        <p class="window-text">{{$localizeFilter('wallet_confirmation_label')}}</p>
        <div class="input-field">
            <input id="code" class="custom-input" :placeholder="$localizeFilter('withdrawal_confirmation_input')" type="text" v-model.trim="code"  :class="{ invalid: v$.code.$error || showEndTimerError}"/>
            <small class="error" v-for="(error, index) of v$.code.$errors" :key="index">
                {{ error.$message }}
            </small>
            <small class="end-timer-error" v-if="showEndTimerError" >
                {{ $localizeFilter('confirmation_code_time_ended') }}
            </small>
            <div class="time">{{$utils.formatTime(timeLeft, false, false)}}</div>
        </div>
        

        <!-- <Toggle style="margin-top: 10px;" :selected="selectedMethod" :title="$localizeFilter('withdrawal_method')+':'" :variants="methods" @toggle="toggle" /> -->
        
        <div class="modal-warning" v-if="commission > 0">
            <i class="material-icons" style="color: #CA8920!important;">report</i>
            <p class="window-text warning">{{ $localizeFilter('commission') + ' ' + commissionFee + ' ' + coinTicker }}</p>
        </div>

        <button class="basic-button submit-button" :class="submitInProgress ? 'inactive' : ''" @click="submitHandler">{{$localizeFilter('confirm')}}</button>
    </div>
    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required, /*numeric, minValue, minLength, maxLength*/ } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'
import WalletOptions from '../WalletOptions.vue';
import utils from '@/utils/utils'

export default defineComponent({
    props: [
        'withdrawId', 'commission', 'coinTicker'
    ],
    validations() {
        return {
            code: {
                // required: helpers.withMessage(({ 
                //     $pending,
                //     $invalid,
                //     $params,
                //     $model
                // })=>`${$pending} ${$invalid} ${$params} ${$model}`, required),
                required: helpers.withMessage(localizeFilter("field_must_not_be_empty"), required),
                numeric: helpers.withMessage(localizeFilter("field_must_be_numeric"), required)
            }
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
    watch: {
        timeLeft(newVal, oldVal) {
            if (newVal <= 0) {
                this.$nextTick(()=>{
                    this.timerEnded()
                })
            }
        }
    },
    data: () => ({
        // amount: "",
        // minAmount: 0,
        // walletAddress: "",
        // walletType: "Bitcoin",
        showEndTimerError: false,
        code: "",
        submitInProgress: false,
    }),
    methods: {
        runTimer() {
            const timestamp = +new Date() + 300000
            this.$store.dispatch('runConfirmationCodeTimer', {timestamp: timestamp})

            document.addEventListener('visibilitychange', function() {
                if(document.hidden) {
                    // tab is now inactive
                    // temporarily clear timer using clearInterval() / clearTimeout()
                    this.$store.dispatch('stopConfirmationCodeTimer')
                }
                else {
                    // tab is active again
                    // restart timers
                    this.$store.dispatch('runConfirmationCodeTimer', {timestamp: timestamp})
                }
            });
        },
        stopTimer() {
            this.$store.dispatch('stopConfirmationCodeTimer')
        },
        timerEnded() {
            this.showEndTimerError = true
        },
        closeModal(reload) {
            this.cancelRequest({ id: this.withdrawId });
            this.$emit("closeModal", reload);
        },
        async cancelRequest(request) {
            try {
                await this.$store.dispatch('cancelWithdrawBalanceRequest', {requestId: request.id})
                this.$message(localizeFilter('application_calceled'))
            } catch (e) {
                this.$error(e)
            }
        }, 
        async submitHandler() {
            this.v$.$touch();
            if (this.v$.$error /*|| !this.conditionsAccepted*/) {
                return;
            }

            // var WAValidator = require('wallet-address-validator');
            // if(!WAValidator.validate(this.walletAddress, this.validatorParam)){
            //     this.$error({message: localizeFilter('incorrect_wallet_address')})
            //     return
            // }

            // send code
            
            this.submitInProgress = true

            try {
                const data = {withdrawBalanceRequestId: this.withdrawId, confirmationCode: this.code}
                await this.$store.dispatch('confirmWithdrawBalanceRequest', data)
                this.$message(localizeFilter('withdraw_successfull'))
                this.$emit('closeModal')
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } catch(e) {
                this.$error(e)
                setTimeout(()=>{
                    this.submitInProgress = false
                },1000)
            } 

        },
        async selectNetwork(network) {
            await this.$store.dispatch('setWithdrawFromWalletNetwork',{network})
            // this.$emit('networkSelected', network)
        },

        getNetworkByTicker(network) {
            if (this.network === null) {return null}
            return this.$store.getters.networkByTicker(network)
        }
    },
    computed: {
        email() {
            return this.$store.getters.profile?.email
        },
        timeLeft(){
            return this.$store.state.info.confirmationCodeTimeLeft
        },
        networksForCoin() {
            return this.$store.getters.networksForCoin(this.coinTicker)
        },
        network() {
            return this.$store.getters.withdrawFromWalletNetwork
        },
        // validatorParam() {
        //     if (this.network === null) {return null}
        //     return this.getNetworkByTicker(this.network).validatorParam
        // },
        // minLength() {
        //     if (this.network === null) {return 0}
        //     return this.getNetworkByTicker(this.network).minLength
        // },
        // maxLength() {
        //     if (this.network === null) {return 100}
        //     return this.getNetworkByTicker(this.network).maxLength
        // },
        commissionFee() {
            console.log(this.$store.getters.assetDecimalsMap[this.coinTicker], this.commission)
            return +utils.displayBigIntAsDecimal(this.commission, this.$store.getters.assetDecimalsMap[this.coinTicker], this.$store.getters.assetDecimalsMap[this.coinTicker].toString().length-1)
        },
    },
    mounted() {
        this.runTimer()
    },
    unmounted() {
        this.stopTimer()
    }
})
</script>


